.nav {
  top: 50px;
  left: 10px;
  display: flex;
  background-color: white;
  padding: 20px 20px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  border-radius: 5px;
  margin: 20px;
}

.nav a {
  text-decoration: none;
  color: rosybrown;
  text-transform: uppercase;
  font-size: 18px;
  text-align: center;
  font-weight: 600;
  margin-left: 4%;
}
